
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { mapGetters } from "vuex";
import api from "../../api/index";

export default {
name: "Home",
components: {
XHeader,
Group,
Cell,
Flexbox,
FlexboxItem
},
data() {
return {
myInfo: {},
headerImage: require("./img/default-user.png"),
base64: ""
};
},

computed: {
...mapGetters(["userInfo"])
},

created() {


},

activated() {
let userid = window.localStorage.getItem("user");
this.$store.dispatch("getInfo", { id: userid });
},


watch: {
userInfo: function(val) {
this.myInfo = val;
if (val.avatar) {
this.headerImage = val.avatar;
}
}
}
};



